import React,{useState} from 'react'
import {Link,useNavigate} from 'react-router-dom'
import Navbar from '../components/Navbar'
export default function Login() {

  const [credentials, setCredentials] = useState({email:"",password:""})
  let navigate = useNavigate()
  const handleSubmit = async (e)=>{
    e.preventDefault();
    console.log('c: ',credentials)
    const response = await fetch("/api/loginuser",{
        method: 'POST',
        headers:{
            'Content-Type':'application/json'
        },
        body:JSON.stringify({email:credentials.email,password:credentials.password})

    });

    const json = await response.json()
    console.log(json);
    if(!json.success){
        alert('Enter valid credentials')
    }

    if(json.success){
      
      localStorage.setItem("authToken",json.authToken);
      localStorage.setItem("userEmail",credentials.email);
      console.log(localStorage.getItem("userEmail"));
      navigate("/");
    }
  }

  const onChange = (e)=>{
    setCredentials({...credentials,[e.target.name]:e.target.value})
  }

  return (
    <div>
      <Navbar />
      <form style={{"marginTop":"15%", "width":"35%"}} className='shadow p-3 mb-5 bg-white rounded container border border-success p-5 rounded' method='POST' onSubmit={handleSubmit}>
            <div className="mb-4">
                <h3 htmlFor="exampleInputEmail1" className="form-label">Login to Account</h3>
                <label htmlFor="exampleInputEmail1" className="form-label text-muted mb-4">Enter your email below to login to your account</label>
                <div className='row justify-content-around mb-3'>
                  <button type="submit" className="btn btn-outline-dark col-5"><i className='bi bi-github m-2'></i>Github</button>
                  <button type="submit" className="btn btn-outline-dark col-5"><i className='bi bi-google m-2'></i>Google</button>
                </div>
                
                <p style={{"textAlign":"center"}} className='text-muted'>------------------------- OR --------------------------</p>
                  
                
                <label htmlFor="exampleInputEmail1" className="form-label">Email address</label>
                <input type="email" className="form-control" name='email' value={credentials.email} onChange={onChange}/>
                <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
            </div>
            <div className="mb-4">
                <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                <input type="password" className="form-control" name='password' value={credentials.password} onChange={onChange} />
            </div>
            <div>
            <button type="submit" className="btn btn-success w-100 ">Login</button>
            </div>
        </form>
        <div className="form-label text-center my-4">New to GoFood? <Link className='text-success text-decoration-none' to="/signup"> <b>Join Now</b></Link></div>
    </div>
  )
}
