import React from "react";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { useState, useEffect } from "react";
// import { AutoAwesome } from '@mui/icons-material'
import { Badge } from 'react-bootstrap';
import cart from "./img/emptycart.png";


export default function MyOrder() {
  const [orderData, setOrderData] = useState("")

    const fetchMyOrder = async () => {
        console.log(localStorage.getItem('userEmail'))
        await fetch("/api/myorderdata", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body:JSON.stringify({
                email:localStorage.getItem('userEmail'),
            })
        }).then(async (res) => {
            let response = await res.json()
            await setOrderData(response)
        })
    }

    useEffect(() => {
        fetchMyOrder()
    }, [])

  return (
    <div>
      <div>
        <Navbar />
      </div>

      <div className="container">
        <div className="row">
          {orderData !== {}
            ? Array(orderData).map((data) => {
                return data.orderData
                  ? data.orderData.order_data.slice(0).reverse().map((item) => {
                        return item.map((arrayData) => {
                          return (
                            <>
                              {arrayData.Order_date ? (
                                <div className="col-md-12">
                                  <div className="m-auto mt-5">{(data = arrayData.Order_date)}<hr /></div>
                                </div>)
                                :( 
                                    <div className="card mt-3 mx-2 col-md-4" style={{width: "16rem",maxHeight: "360px",}}>
                                        <div className="card-body btn-outline-success" >
                                          <h5 className="card-title">{arrayData.name}</h5>
                                          <div className="container p-0" style={{ height: "38px",  width:"250px" }}>
                                            <span className="">{arrayData.qty}</span>
                                            <span className="m-1">{arrayData.size}</span><br />
                                            <span className="">{data}</span>
                                          </div>
                                          <div>
                                          <Badge pill bg="success ml-2 p-2 mt-3">₹{arrayData.price}/-</Badge>
                                        </div>
                                    </div>
                                    </div>
                                  
                                   
                              )}
                              
                            </>
                          );
                        });
                      })
                  : <div className="m-5 w-100 text-center">
                  <img src={cart}></img>
                  <label className="form-label fs-4"><b>Oops! Your Plate is Empty</b></label>
                  <br></br>
                  <label className="form-label text-muted">fill it up by placing your first GoFood order</label>
                  
              </div>;
              })
            : ""}
        </div>
      </div >
      <div style={{"position":"fixed","width":"100%","bottom":"0px"}}>
      </div>
    </div>
  );
}
