import React from 'react'
import { useEffect } from 'react';
import { useRef } from 'react';
import { useState } from 'react';
import { useDispatchCart, useCart } from './ContextReducer';

export default function Card(props) {
  let dispatch = useDispatchCart();
  let data = useCart();
  const priceRef = useRef();
  let options = props.options;
  let priceOptions = Object.keys(options)
  const [size, setSize] = useState("")
  const [qty, setQty] = useState(1)

  const handleAddToCart = async ()=>{
    let food = []
    for (const item of data){
      if (item.id === props.foodItem._id){
        food=item;
        break;
      }
    }
    if (food !== []){
      if (food.size === size){
        await dispatch({type:'UPDATE',id:props.foodItem._id, price: finalPrice, qty:qty})
        return
      }
      else if (food.size !== size){
        await dispatch({type:"ADD",id:props.foodItem._id,name:props.foodItem.name,price:finalPrice, qty:qty, size:size})
        return
      }
      return
    }
    await dispatch({type:"ADD",id:props.foodItem._id,name:props.foodItem.name,price:finalPrice, qty:qty, size:size})

  }
  let finalPrice = qty * parseInt(options[size]);

  useEffect(()=>{
    setSize(priceRef.current.value)
  },[])
  return (
      <div className="card m-3"  style={{"width": "18rem", "maxHeight":"550px"}}>
        <img src={props.foodItem.img} className="card-img-top" style={{"height":"200px", "objectFit":"fill"}} alt="..." />
        <div className="card-body">
            <h5 className="card-title">{props.foodItem.name}</h5>
            <div style={{"marginLeft":"-29px", "width":"101%"}} className="container">
              <select className="m-2 h-100 bg-success rounded text-white p-1" onChange={(e)=> setQty(e.target.value)}>
                {
                  Array.from(Array(6),(e,i)=>{
                    return (
                      <option key={i+1} value={i+1}>{i+1}</option>
                    )
                  })
                }
              </select>
              <select ref={priceRef} className="m-2 h-100  bg-success rounded text-white p-1" onChange={(e)=> setSize(e.target.value)}>
                 {
                    priceOptions.map(data=>{
                    return(
                      <option key={data} value={data}>{data}</option>
                    )
                    
                  })
                } 
              </select>
              <div className='d-inline h-100 fs-5'>Rs {finalPrice}.00</div>
            </div>
            <hr/>
            <button onClick={handleAddToCart} className="btn btn-success">Add to cart</button>
        </div>
        </div>
  )
}
