import React from 'react'
import { useCart, useDispatchCart } from '../components/ContextReducer'
import Button from 'react-bootstrap/Button';
import binIcon from './img/bin.png'
import Navbar from '../components/Navbar';
import cart from "./img/emptycart.png";

export default function Cart() {
    let data = useCart();
    let dispatch = useDispatchCart();
    if (data.length===0){
        return (

            <div>
                <Navbar />
                <div className="m-5 w-100 text-center">
                    <img src={cart}></img>
                    <label className="form-label fs-4"><b>Your Plate is Empty!</b></label>
                    <br></br>
                    <label className="form-label text-muted">fill it up by placing your order and have it delivered at your doorstep</label>
                    
                </div>
                <div className=" w-100 text-center fs-6 text-muted"></div>
            </div>
        )
    }

    const handleCheckOut = async()=>{
        let userEmail = localStorage.getItem("userEmail");
        console.log(localStorage)
        let response = await fetch("/api/orderdata",{
            method: 'POST',
            headers:{
                'Content-Type':'application/json'
            },
            body:JSON.stringify({
                order_data: data,
                email:userEmail,
                order_date: new Date().toDateString()
            })
        });
        console.log("res:",response)
        if (response.status===200){
            dispatch({type:"DROP"})
        }
    }

    let totalPrice = data.reduce((total, food)=> total+food.price,0)
  return (
    <div>
        <Navbar/>
        <div className="container m-auto mt-5 table-responsive table-responsive-sm table-responsive-md">
            <table className="table table-hover">
                <thead className="text-success fs-4">
                    <tr>
                        <th scope="col">#</th>
                        <th scope="col">Name</th>
                        <th scope="col">Quantity</th>
                        <th scope="col">Option</th>
                        <th scope="col">Amount</th>
                        <th scope="col">Amount</th>
                    </tr>
                </thead>
                <tbody>
                    
                    {data.map((food,index) =>{
                        return(
                            <tr>
                            <th scope='row'>{index+1}</th>
                            <td>{food.name}</td>
                            <td>{food.qty}</td>
                            <td>{food.size}</td>
                            <td>{food.price}</td>
                            <td><Button type='button' className='bg-white border-white' onClick={()=>{dispatch({type:"REMOVE",index:index})}}><img style={{"width":"30px", "height":"30px"}} src={binIcon}/></Button></td>
                        </tr>
                        )
                       
                     })

                    }
                </tbody>
            </table>
            <div><h1 className="fs-2">Total Price: {totalPrice}/-</h1></div>
            <div>
                <button onClick={handleCheckOut} className="btn btn-success mt-5">Check Out</button>
            </div>
        </div>
    </div>
  )
}
