import React, { useState,useEffect } from 'react'
import Navbar from '../components/Navbar'
import Footer from '../components/Footer'
import Card from '../components/Card'
import { useNavigate} from 'react-router-dom'
import pic1 from "./img/bg.jpg"

export default function Home() {

  const [foodCategory, setFoodCategory] = useState([]);
  const [foodItems, setFoodItems] = useState([]);
  const [search, setSearch] = useState('');
  const navigate = useNavigate();
  const loadData = async ()=>{
    let response = await fetch("/api/foodData",{
      method: 'POST',
      headers: {
        'Content-Type':'application/json'
      }
    });
    response = await response.json();
    console.log(response[0],response[1])
    setFoodItems(response[0]);
    setFoodCategory(response[1]);
  }

  useEffect(()=>{
    (localStorage.getItem("authToken")) ? loadData():navigate("/login")
  },[])

  return (
    <div>
        <div><Navbar /></div>
        <div>
        <div id="carouselExampleFade" className="carousel slide carousel-fade" style={{'objectFit':'contain !important'}}>
            <div className="carousel-inner" id="carousel">
                <div className="carousel-caption" style={{"zIndex":"10"}}>
                <div className="d-flex justify-content-center">
                    <input value={search} onChange={(e)=>{setSearch(e.target.value)}} className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
                </div>
                </div>
                <div className="carousel-item active">
                <img src={pic1} style={{filter: "brightness(30%)"}} className="d-block w-100 h-10" alt="..." />
                </div>
                <div className="carousel-item">
                <img src={pic1} style={{filter: "brightness(30%)"}} className="d-block w-100" alt="..." />
                </div>
                <div className="carousel-item">
                <img src={pic1} style={{filter: "brightness(30%)"}} className="d-block w-100" alt="..." />
                </div>
            </div>
            <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleFade" data-bs-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="visually-hidden">Previous</span>
            </button>
            <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleFade" data-bs-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="visually-hidden">Next</span>
            </button>
            </div>
        </div>
        <div className='container'>
          {
            foodCategory !==[]
            ? foodCategory.map((data)=>{
              return (
                <div className='row mb-3'>
                  <div className='fs-3 m-3 col-md-12' key={data._id}>
                    {data.CategoryName}
                  </div>
                  <hr />
                  {
                    foodItems!==[]
                    ? foodItems.filter((item)=> item.CategoryName == data.CategoryName && (item.name.toLowerCase().includes(search.toLowerCase())))
                    .map(filterItems=>{
                      return (
                        <div className='col-12 col-md-4 col-lg-3 m-3' key={filterItems._id}>
                          <Card foodItem={filterItems}
                          options={filterItems.options[0]}
                          />
                        </div>
                      )
                    })
                    :<div>No data Found</div>
                  }
                </div>
                )
            }):<div>hello</div>
          }
        </div>
        <div><Footer /></div>
    </div>
  )
}
