import React from 'react'
import {Link, useNavigate} from 'react-router-dom'
import { Badge } from 'react-bootstrap';
import { useCart } from './ContextReducer';

export default function Navbar() {
  let data = useCart();
  const navigate = useNavigate();
  const handleLogout = ()=>{
    localStorage.removeItem("authToken");
    navigate("/login")
  }
  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-dark bg-success">
  <div className="container-fluid">
    <Link className="navbar-brand fs-1" to="/">GoFood</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarNav">
      <ul className="navbar-nav me-auto mb-2">
        <li className="nav-item">
          <Link className="nav-link active fs-5" aria-current="page" to="/">Home</Link>
        </li>
        {/* <li className="nav-item">
          <Link className="nav-link active fs-5" aria-current="page" to="/">About</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active fs-5" aria-current="page" to="/">Contact</Link>
        </li> */}
        {
          (localStorage.getItem("authToken"))?
          <li className="nav-item">
          <Link className="nav-link active fs-5" aria-current="page" to="/MyOrder">My Orders</Link>
        </li>
        :""
        }
        
      </ul>
      
        {
          (localStorage.getItem("authToken"))
          ?<div>
            <Link className="btn bg-white text-success mx-1" to="/cart">My Cart <Badge pill bg="danger ml-2 mr-2">{data.length}</Badge></Link>
            <div className="btn bg-white text-success mx-1" onClick={handleLogout}>Logout</div>
          </div>
          :<div className='d-flex'>
            <Link className="btn bg-white text-success mx-1" to="/login">Login</Link>
            <Link className="btn bg-white text-success mx-1" to="/signup">Sign Up</Link>
          </div>
        }
        
      
    </div>
  </div>
</nav>
    </div>
  )
}
