import React, {useState} from 'react'
import {Link} from 'react-router-dom'
export default function Signup() {

  const [credentials, setCredentials] = useState({name:"",email:"",location:"",password:""})

  const handleSubmit = async (e)=>{
    e.preventDefault();
    console.log(credentials)
    const response = await fetch("/api/createuser",{
        method: 'POST',
        headers:{
            'Content-Type':'application/json'
        },
        body:JSON.stringify({name:credentials.name,email:credentials.email,password:credentials.password,location:credentials.location})
    });

    const json = await response.json()
    console.log(json);
    if (json.success){
        alert('Account created')
    }
    if(!json.success){
        alert('Enter valid credentials')
    }
  }

  const onChange = (e)=>{
    setCredentials({...credentials,[e.target.name]:e.target.value})
  }


  return (
    <div className='container'>
        <form style={{"marginTop":"15%", "width":"50%"}} className='shadow p-3 mb-5 bg-white container border border-success p-5 rounded' method='POST' onSubmit={handleSubmit}>
            
            <div className="mb-4">
                <h3 htmlFor="exampleInputEmail1" className="form-label">Create an account</h3>
                <label htmlFor="exampleInputEmail1" className="form-label text-muted mb-4">Enter your email below to create your account</label>
                <div className='row justify-content-around mb-3'>
                  <button type="submit" className="btn btn-outline-dark col-5"><i className='bi bi-github m-2'></i>Github</button>
                  <button type="submit" className="btn btn-outline-dark col-5"><i className='bi bi-google m-2'></i>Google</button>
                </div>
                
                <p style={{"textAlign":"center"}} className='text-muted'>-------------------------------- OR ---------------------------------</p>
        
                <label htmlFor="name" className="form-label">Name</label>
                <input type="text" className="form-control" name='name' value={credentials.name} onChange={onChange} />
            </div>

            <div className="mb-4">
                <label htmlFor="exampleInputEmail1" className="form-label">Email address</label>
                <input type="email" className="form-control" name='email' value={credentials.email} onChange={onChange}/>
                <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
            </div>
            
            <div className="mb-4">
                <label htmlFor="location" className="form-label">Location</label>
                <input type="text" className="form-control" name='location' value={credentials.location} onChange={onChange} />
            </div>

            <div className="mb-4">
                <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                <input type="password" className="form-control" name='password' value={credentials.password} onChange={onChange} />
            </div>
            <div>
                <button type="submit" className="btn btn-success w-100 ">Sign Up</button>
            </div>
        </form>
        <div className="form-label text-center my-4">Already a User? <Link className='text-success text-decoration-none' to="/login"> <b>Sign In</b></Link></div>
    </div>
  )
}
